.educationContainer {
  margin-top: 0px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.educationBanner {
  max-width: 100%;
  padding: 0px;
  margin: auto;
  height: 300px;
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.educationBannerContainer {
  top: 60px;
  margin: auto;
  position: relative;
  text-align: center;
}

.educationBannerText {
  font-size: 300%;
  color: floralwhite;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -80%);
      -ms-transform: translate(-50%, -80%);
          transform: translate(-50%, -80%);
  border: 1px solid floralwhite;
  padding: 20px;
}

.resourceLink {
  text-decoration: underline;
}

.resources {
  margin-top: 56px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

.educationImg {
  width: 450px;
  height: 700px;
  -o-object-fit: cover;
     object-fit: cover;
} 

.resource {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  border-top: 1px solid rgb(0 0 50);
}

.educationText {
  margin-top: 20px;
  text-align: center;
  max-width: 800px;
  padding: 0px 20px 0px 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 0px 40px 10px 40px;
}

@media only screen and (max-device-width: 480px) {
  .educationImg {
    width: 50%;
    height: 600px;
    -o-object-fit: cover;
       object-fit: cover;
  } 

  .educationText {
    padding: 0px 10px 10px 10px;
  }

  .educationBanner {
    height: 150px; 
  }  

  .educationBannerText {
    margin-top: 30px;
    font-size: 200%;
  }
}


